<template>
  <div class="home">
    <div class="loginBox" v-if="status===false">
      <div class="title">请输入密码</div>
      <input v-model="password" type="password" placeholder="请输入密码"/>
      <van-button block class="button" type="primary" @click="submit">查看文档</van-button>
    </div>
    <doc v-if="status"></doc>
  </div>
</template>

<script>
// import config from "./config";

import Doc from "./views/doc";

export default {
  components: {Doc},
  data() {
    return {
      status: false,
      password: '',
    };
  },
  mounted() {
    this.$store.dispatch('checkPassword', this.password).then(res => {
      this.status = res.status;
    })
  },
  methods: {
    submit() {
      if (!this.password) {
        this.$toast('请输入文档密码！');
      }
      this.$store.state.password = this.password;
      this.$store.dispatch('login', this.password).then(res => {
        this.status = res.status;
      })
    }
  }
}
</script>
