            <ul class="module-lists">
              <li v-for="(d,index) in m" :key="index" @click="choseApi(key,index)" :class="{
                curr:(api==index && key === module)
              }">
                {{ d.summary }}
              </li>
            </ul>
          </div>
        </div>
        <div v-else style="color: #444;">
          <van-empty description="未选择项目"/>
        </div>
      </div>
      <div class="appSelect">
        <van-button block size="small" type="primary" @click="show=true">切换项目接口</van-button>
      </div>
    </div>

    <div class="right-body" style="display: flex;flex-direction: column;flex:1;height: 100vh;padding-right: 70px">
      <!--      <div class="header" style="background: rgb(255, 255, 255);height: 60px;box-shadow: 8px 8px 8px #eee;">-->
      <!--        123456-->
      <!--      </div>-->
      <div class="body" style="flex: 1;overflow-y: auto;">
        <div class="box" v-if="!module||!api"
             style="display: flex;height: 100%;align-items: center;justify-content: center">
          <div style="font-size: 120px;color: #BBB;font-weight: 700;text-align: center;padding-bottom: 25%">
            QApi
          </div>
        </div>
        <div class="box" v-if="module && api!=null">
          <div class="title">
            {{ doc[choseApp][choseHost].doc[choseVersion][module][api].summary }}
          </div>

          <div class="description">
            {{ doc[choseApp][choseHost].doc[choseVersion][module][api].description }}
          </div>
          <div class="title">
            接口地址
          </div>
          <div class="description">
            <van-tag type="primary" plain size="large">{{
                doc[choseApp][choseHost].doc[choseVersion][module][api].type
              }}
            </van-tag>
            &nbsp; &nbsp;
            <span style="font-size: 16px;">{{ doc[choseApp][choseHost].scheme }}://{{
                doc[choseApp][choseHost].host
              }}{{ doc[choseApp][choseHost].doc[choseVersion][module][api].path }}</span>
          </div>
          <div class="title">
            请求参数
          </div>
          <div class="description">
            <table class="apiTable" border="0" cellspacing="0" cellpadding="0">
              <thead>
              <tr>
                <td>参数</td>
                <td>简介</td>
                <td>参数位置</td>
                <td>参数类型</td>
                <td>必填</td>
                <td>参数值</td>
                <td style="width: 300px;">描述</td>
              </tr>
              </thead>
              <tbody>
              <template v-for="(params,k) in doc[choseApp][choseHost].doc[choseVersion][module][api].params">
                <template
                  v-if="k==='QApi\\Attribute\\Parameter\\HeaderParam' || k==='QApi\\Attribute\\Parameter\\GetParam' || k==='QApi\\Attribute\\Parameter\\PathParam'||k==='QApi\\Attribute\\Parameter\\PostParam'">
                  <tr :key="i" v-for="(data,i) in params">
                    <td>
                      {{ data.name }}
                    </td>
                    <td>
                      {{ data.summary || '无' }}
                    </td>
                    <td>
                      <van-tag v-if="k=='QApi\\Attribute\\Parameter\\HeaderParam'" size="large" type="primary" plain>
                        header
                      </van-tag>
                      <van-tag v-else-if="k=='QApi\\Attribute\\Parameter\\PathParam'" size="large" type="danger" plain>
                        path
                      </van-tag>
                      <van-tag v-else-if="k=='QApi\\Attribute\\Parameter\\GetParam'" size="large" type="success" plain>
                        query
                      </van-tag>
                      <van-tag v-else-if="k=='QApi\\Attribute\\Parameter\\PostParam'" size="large" type="warning" plain>
                        form-data
                      </van-tag>
                    </td>
                    <td>
                      {{ data.type }}
                    </td>
                    <td>
                      {{ data.required }}
                    </td>
                    <td>
                      <form-file v-model="data.default" v-if="data.type==='file'" class="file" :class="{
                      // 'danger':data.required === true&&!data.default
                    }"/>
                      <textarea placeholder="参数值" v-model="data.default" :class="{
                      // 'danger':data.required === true&&!data.default
                    }" v-else/>
                    </td>
                    <td>
                      {{ data.description || '无' }}
                    </td>
                  </tr>
                </template>
              </template>
              </tbody>
            </table>
          </div>
          <div class="title" style="max-width: 1200px">
            <van-button size="small" style="padding: 0 30px;" type="primary"
                        @click="run(doc[choseApp][choseHost].doc[choseVersion][module][api])">运行
            </van-button>
            <van-button @click="saveResponse(doc[choseApp][choseHost].doc[choseVersion][module][api])" size="small"
                        style="padding: 0 30px;float: right" type="default"
                        v-if="doc[choseApp][choseHost].doc[choseVersion][module][api].response">更新示例
            </van-button>
          </div>
          <div class="description">
            <vue-json-pretty :path="'res'" :data="doc[choseApp][choseHost].doc[choseVersion][module][api].response"
                             v-if="doc[choseApp][choseHost].doc[choseVersion][module][api].response"></vue-json-pretty>
          </div>
        </div>
      </div>
    </div>
    <van-icon name="replay" size="18"
              color="#222"
              style="position:absolute;cursor:pointer;right: 30px;bottom: 90px;background: #fff;width: 40px;height: 40px;border:1px solid #eee;box-shadow: 6px 6px 6px #aaa;border-radius: 40px;text-align: center;line-height: 40px;"
              @click="refreshApi" class="refreshApi"/>
    <van-icon name="revoke" size="18"
              color="#222"
              style="position:absolute;cursor:pointer;right: 30px;bottom: 30px;background: #fff;width: 40px;height: 40px;border:1px solid #eee;box-shadow: 6px 6px 6px #aaa;border-radius: 40px;text-align: center;line-height: 40px;"
              @click="logout" class="logout"/>
    <van-popup v-model="show" round position="bottom">
      <van-cascader
        @change="change"
        v-model="cascaderValue"
        title="请选择项目"
        active-color="#07c160"
        :options="options"
        @close="show = false"
        @finish="choseFinish"
      />
    </van-popup>
