<script>
import VueJsonPretty from 'vue-json-pretty';
import 'vue-json-pretty/lib/styles.css';
import FormFile from "./FormFile";


export default {
  name: "doc",
  components: {FormFile, VueJsonPretty},
  data() {
    return {
      show: false,
      doc: null,
      choseApp: this.$route.query.app || null,
      choseHost: this.$route.query.host || null,
      choseVersion: this.$route.query.version || null,
      module: this.$route.query.m || null,
      api: this.$route.query.api || null,
      apiData: null,
      cascaderValue: '',
      options: [],
    };
  },
  created() {
    this.loadDoc();
  },
  methods: {
    choseApi(m, api) {
      this.$router.replace({
        path: '/',
        query: {
          app: this.choseApp,
          host: this.choseHost,
          version: this.choseVersion,
          m: m,
          api: api,
        }
      });
    },
    logout(){
      this.$store.dispatch('logout').then(()=>{
        location.href='./';
      });
    },
    loadResponse(api) {
      if (!api.response) {
        this.$http.getJSON('__apiResponse.json', {
          path: this.choseApp + '##' + this.doc[this.choseApp][this.choseHost].host + api.path,
          type: api.type,
        }).then(res => {
          api.response = JSON.parse(res.data);
        });
      }
    },
    saveResponse(api) {
      this.$http.post('__apiResponse.json', {
        path: this.choseApp + '##' + this.doc[this.choseApp][this.choseHost].host + api.path,
        type: api.type,
      }, {
        response: JSON.stringify(api.response),
      }).then(res => {
        if (res.status) {
          this.$toast('保存成功！');
        }
      });
    },
    run(api) {
      let load = this.$toast.loading('运行中');
      this.$http.request(api, this.doc[this.choseApp][this.choseHost].scheme + '://' + this.doc[this.choseApp][this.choseHost].host).then(res => {
        api.response = res.data;
      }).then(()=>{
        return this.$notify({
          type:'success',
          message:'接口运行成功!',
        });
      }).finally(()=>{
        load.clear();
      });
    },
    choseFinish(d) {
      this.show = false;
      this.choseApp = d.selectedOptions[0].value;
      this.choseHost = d.selectedOptions[1].value;
      this.choseVersion = d.selectedOptions[2].value;
      this.$router.replace({
        path: '/',
        query: {
          app: this.choseApp,
          host: this.choseHost,
          version: this.choseVersion,
        }
      });
    },
    refreshApi() {
      let toast = this.$toast.loading('正在刷新');
      this.$http.post('__apis.json', {}, {}).then(res => {
        if(!res.status){
          return this.$notify({
            type:'danger',
            message:res.msg,
          });
        }

        let options = [];
        this.doc = res.data;
        for (let key in this.doc) {
          let hosts = [];
          for (let host in this.doc[key]) {
            let versions = [];
            for (let version in this.doc[key][host].doc) {
              versions.push({
                text: '版本-' + version,
                value: version,
              });
            }
            hosts.push({
              text: this.doc[key][host].host + ' - ' + this.doc[key][host].mode,
              value: host,
              children: versions,
            });
          }
          let app = {
            text: key,
            value: key,
            children: hosts,
          };
          options.push(app);
        }
        this.options = options;
        if(this.api){
          this.loadResponse(this.doc[this.choseApp][this.choseHost].doc[this.choseVersion][this.module][this.api]);
        }

        return this.$notify({
          type:'success',
          message:res.msg,
        });
      }).finally(() => {
        toast.clear();
      });
    },
    loadDoc() {
      this.$http.getJSON('__apis.json').then(res => {
        let options = [];
        this.doc = res.data;
        for (let key in this.doc) {
          let hosts = [];
          for (let host in this.doc[key]) {
            let versions = [];
            for (let version in this.doc[key][host].doc) {
              versions.push({
                text: '版本-' + version,
                value: version,
              });
            }
            hosts.push({
              text: this.doc[key][host].host + ' - ' + this.doc[key][host].mode,
              value: host,
              children: versions,
            });
          }
          let app = {
            text: key,
            value: key,
            children: hosts,
          };
          options.push(app);
        }
        this.options = options;
        if (this.api != null) {
          this.loadResponse(this.doc[this.choseApp][this.choseHost].doc[this.choseVersion][this.module][this.api]);
        }
      });
    },
  },
  watch: {
    $route() {
      this.choseApp = this.$route.query.app || null;
      this.choseHost = this.$route.query.host || null;
      this.choseVersion = this.$route.query.version || null;
      this.module = this.$route.query.m || null;
      this.api = this.$route.query.api || null;
      if (this.api) {
        this.loadResponse(this.doc[this.choseApp][this.choseHost].doc[this.choseVersion][this.module][this.api]);
      }
    }
  }
}
</script>

